import React from 'react'
import { Link } from 'react-router-dom';

import BookCard from './BookCard';

export default function List(props) {
    
  const {
    list,
    toggleFav,
    deleteBook
  } = props;

  return (
    <div>
      <h2 className="list-heading">All Books</h2>
      <div className="d-flex flex-wrap">
        {list.length > 0 ? (
          list.map((book, index) => <BookCard key={book.bookName+index} {...book} toggleFav={toggleFav} deleteBook={deleteBook}/>)
        ) : (
          <h3>
            No books here. Please checkout <Link to="/add">Add Books</Link> section.
          </h3>
        )}
      </div>
    </div>
  )
}
