import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';


import BookCard from './BookCard'

function Favs(props) {
  const {
    list,
    removeFav
  } = props;

  return (
    <div>
      <h2 className="list-heading">Favourites</h2>
      <div className="d-flex flex-wrap">
        {list.length > 0 ? (
          list.map((book, index) => <BookCard key={book.bookName+index} {...book} deleteBook={removeFav}/>)
        ) : (
          <h3>
            No books here. Please checkout <Link to="/books">All Books</Link> section.
          </h3>
        )}
      </div>
    </div>
  )
}

Favs.propTypes = {
  list: PropTypes.array,
  removeFav: PropTypes.func
}

export default Favs

