import React, {Component} from 'react';
import { Route, Switch } from 'react-router-dom';


import Sidebar from './components/Sidebar';
import Add from './components/Add';
import List from './components/List';
import Favs from './components/Favs';

export default class App extends Component {
  
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
    this.state = JSON.parse(localStorage.getItem('booksData')) || {};
    this.state.count = 0;
    this.state.favCount = 0;
    if(localStorage.getItem('booksData') === null){
      localStorage.setItem('booksData', '{"record":[]}');
    }

    this.updateBooksData = this.updateBooksData.bind(this);
    this.toggleFav = this.toggleFav.bind(this);
    this.removeFav = this.removeFav.bind(this);
    this.deleteBook = this.deleteBook.bind(this);
    this.syncLS = this.syncLS.bind(this);
  }

  toggleFav(name){
    let currentRec = this.state.record;

    [].forEach.call(currentRec, entry => {
      if(entry.bookName === name){
        entry.isFav = !entry.isFav;
        return;
      }
    });

    this.setState(state => {
      state.record = currentRec
    });

    this.syncLS();
  }

  removeFav(name){

    let currentRec = this.state.record;

    [].forEach.call(currentRec, entry => {
      if(entry.bookName === name){
        entry.isFav = false;
        return;
      }
    });

    this.setState(state => {
      state.record = currentRec
    }, this.syncLS);

  }

  deleteBook(name,ev){
    let currentRec = this.state.record;

    this.setState(state => {
      state.record = currentRec.filter(entry => entry.bookName !== name)
    }, this.syncLS);

  }

  syncLS(){
    localStorage.setItem('booksData', JSON.stringify(this.state));
    this.forceUpdate();
  }

  updateBooksData(booksObj){
    let existing = JSON.parse(localStorage.getItem('booksData'));
    existing.record.push(booksObj);
    this.setState({...existing});
    localStorage.setItem('booksData', JSON.stringify(existing));
  }

  render(){
    return (
      <div className="wrapper">
        <div className="sidebar-wrapper">
          <Sidebar count={this.state.record.length} favCount={this.state.record.filter(b => b.isFav).length}/>
        </div>
        <div className="content-wrapper">
          <Switch>
            <Route path="/add" exact render={props => <Add storeBook={this.updateBooksData} />} />
            <Route path="/books" render={props => <List deleteBook={this.deleteBook} toggleFav={this.toggleFav} list={this.state.record} />} />
            <Route path="/favourites" render={props => <Favs removeFav={this.removeFav} list={this.state.record.filter(item => item.isFav)} />} />
          </Switch>
        </div>
      </div>
    )
  }
}
