import React, {Component} from 'react'
import PropTypes from 'prop-types'

class Add extends Component{

  constructor(props){
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.state = {
      bookName: '',
      author: '',
      price: 0,
      isFav : false,
      invalid: {
        bookName: true,
        author: true,
        price: true,
      }
    };
  }


  handleSubmit(e){
    let hasErrors = false;
    [].forEach.call(Object.keys(this.state.invalid), ans => {

      if(this.state.invalid[ans] === true)
        hasErrors = true;
    });

    if(hasErrors){
      alert('No data added');
    }
    else{
      alert('Book added');
      this.props.storeBook(this.state);
    }
  }

  handleChange(e){
    let fieldName = e.target.name;
    let val = e.target.value;

    this.setState(state => {
      state[fieldName] = val
    });

    let errorMsg = '';
    if(e.target.type === 'text'){
      if(val.match(/^[\w\-\s]+$/) === null && val ===''){
        errorMsg = "Invalid Data";
        this.setState(state => {
          state.invalid[fieldName] = true;
        });
      }
      else{
        errorMsg = ' ';
        this.setState(state => {
          state.invalid[fieldName] = false;
        });
      }
    }
    else if(e.target.type === 'number'){
      if(val < 0 || val > 999){
        errorMsg = "Invalid Data";
        this.setState(state => {
          state.invalid[fieldName] = true;
        });
      }
      else{
        errorMsg = ' ';
        this.setState(state => {
          state.invalid[fieldName] = false;
        });
      }
    }
    e.target.nextElementSibling.innerText = errorMsg; 
  }

  render(){
    return (
      <div className="d-flex justify-content-center align-items-center full-height">
        <div className="card add-form" onSubmit={this.handleSubmit}>

          <h1>
            Add Book
          </h1>

          <hr/>

          <div className="form-container">
            <div className="relative">
              <label htmlFor="bookName">Book Name</label>
              <input type="text" defaultValue={this.state.bookName} maxLength={50} minLength={1} name='bookName' onBlur={this.handleChange}/>
              <p className="error"></p>
            </div>
            
            <div className="relative">
              <label htmlFor="author">Author</label>
              <input type="text" maxLength={50} minLength={1} name='author' onBlur={this.handleChange}/>
              <p className="error"></p>
            </div>
    
            <div className="relative">
              <label htmlFor="price">Price in USD</label>
              <input type="number" min={0} max={9999999} step={0.01} onBlur={this.handleChange} name='price'/>
              <p className="error"></p>
            </div>
          
            <input type="submit" onClick={this.handleSubmit}/>

          </div>
        </div>
      </div>
    )
  }
}

Add.propTypes = {
  storeBook : PropTypes.func
}

export default Add

