import React from 'react'
import PropTypes from 'prop-types'
import { NavLink } from 'react-router-dom'

import logo from '../assets/logo.png'
import books from '../assets/add books default.png'
import fav from '../assets/like.png'
import add from '../assets/books.png'
import books_selected from '../assets/add book select.png'
import fav_selected from '../assets/favourite select.png'
import add_selected from '../assets/add books select.png'


function Sidebar(props) {
  return (
    <div>
      <div className="side-content">
        <img src={logo} alt="Logo" className="logo"/>

        <nav>
          <ul>
            <li>
              <NavLink to="/add" activeClassName="active">
                <img src={add} alt="add book" className="navbar-icon"/>
                <img src={add_selected} alt="add book" className="navbar-icon show-on-active"/>
                Add Books
              </NavLink>
            </li>
            <li>
              <NavLink to="/books" activeClassName="active">
                <img src={books_selected} alt="add book" className="navbar-icon show-on-active"/>
                <img src={books} alt="all books" className="navbar-icon"/>
                Books
                {props.count ? <span className="count">{props.count}</span> : ''}
              </NavLink>
            </li>
            <li>
              <NavLink to="/favourites" activeClassName="active">
                <img src={fav_selected} alt="add book" className="navbar-icon show-on-active"/>
                <img src={fav} alt="fav books" className="navbar-icon"/>
                Favourites
                {props.favCount ? <span className="count">{props.favCount}</span> : ''}
              </NavLink>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  )
}

Sidebar.propTypes = {
  count: PropTypes.number,
  favCount: PropTypes.number
}

export default Sidebar

