import React from 'react'
import PropTypes from 'prop-types'

import deleteIcon from '../assets/delete.png';
import banner from '../assets/book dummy.png';

function BookCard(props) {

  const {
    bookName,
    author,
    price,
    isFav
  } = props;

  return (
    <div className="card book-card">
      <div className="text-right">
        {props.toggleFav ? (
            <label className="pull-left">
              <input type="checkbox" defaultChecked={isFav} onChange={() => props.toggleFav(bookName)}/>
              Add to favourite
            </label> 
        ) : ''}
        
        <span>
          <img src={deleteIcon} alt="Delete" onClick={(ev) => props.deleteBook(bookName,ev)}/>
        </span>
      </div>
      <div className="banner">
        <img src={banner} alt="book cover"/>
      </div>
      <div>
        <h3 className="name">
          {bookName}
        </h3>
        <span className="pull-left">
          By {author}
        </span>
        <span className="pull-right price">
          $ {price}
        </span>
      </div>
    </div>
  )
}

BookCard.propTypes = {
  bookName : PropTypes.string,
  author : PropTypes.string,
  price : PropTypes.string,
  isFav : PropTypes.bool,
  toggleFav : PropTypes.func,
  deleteBook : PropTypes.func,
}

export default BookCard

